<template>
  <div class="next">
    <div :class="['content', state, size ]">
      <small> {{ next.module }}</small>
      <h4 > {{ next.title }}</h4>
      <Arrow class="fl"
        @click.native="goToNext(next.id)"/>
    </div>
  </div>
</template>

<script>

import Arrow from 'vue-material-design-icons/ArrowRight.vue'

export default {
 name: 'Next',
 components:{
   Arrow
},
 props:{
   info: Object,
   next: Object,
   small: Boolean
 },
 computed: {
   state () {
     return this.next.state ? 'on' : 'off'
   },
   size () {
     return this.small ? 's' : ''
   }
 },
 methods: {
   goToNext (module) {
      if (!this.next.state) { return }
      window.location.href = `/dash/${module}`;
   }
 }
}
</script>

<style scoped lang="scss">
  .next{
    @include flex;
    @include edItem(100,100,30);
    @include fromTo(s,l){
      display: none;
    }
    .content{
      position: relative;
      width: 100%;
      @include mainStart;
      flex-direction: column;
      align-items: flex-start;
      padding: 1em 2em;
