    }
    small{
      font-size: 1em;
      font-weight: 600;
    }
    h4{
      font-size: 1.5em;
      font-weight: 200;
      font-style: italic;
    }
    .fl{
      position: absolute;
      font-size: 2em;
      right: 0.75em;
    }

    .on{
      background-color: var(--color00);
      border: 2px solid var(--color01);
