<template>
    <div class="tablaScope">
      <h3 class="title">Tabla de contenidos</h3>
      <ul>
        <li v-for="(video, index) in videos" :key="index"
          @click="select(video.url)">
          <Button
           :on="first(video.id)"
           model="a5"
           size="m"
           group="videos">
          {{ video.name }}
          </Button>
        </li>
      </ul>
    </div>
</template>

<script>

import Bus from '@/helpers/Bus'
import Button from '@/components/Button.vue'

export default {
  name: 'TablaContenidos',
  components:{
    Button
  },
  props: {
    videos: Array
  },
  methods: {
    select(url) {
      Bus.$emit('goVide', url);
    },
    first (id) {
      if (id == 1) { return true }
      else { return false }
    }
  }
}
</script>

